/* #region Default styles */
body {
    margin: 0;
    padding: 0 0 100px 0;
    font-family: sans-serif;
    overflow-x: hidden; }
  
  .App {
    text-align: center;
    position: relative; }
  
  .App-logo {
    height: 80px; }
  
  /* DEMO STYLES */
  .nav {
    padding: 30px 0;
    background-color: #222; }
  
  .nav a {
    display: inline-block;
    width: 100px;
    text-align: center;
    margin: 0 15px;
    padding: 10px;
    border: 1px rgba(255, 255, 255, 0.5) solid;
    transition: all 0.3s linear;
    text-decoration: none;
    color: white; }
  
  .nav a.active {
    background-color: #61DAFB;
    color: #222; }
  
  section {
    padding: 20px 0;
    max-width: 600px;
    margin: 0 auto; }
  
  /* #endregion */
  /* #region Card */
  /* Card */
  .card {
    border: 3px #f3f3f3 solid;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.3);
    transition: all 0.03s linear;
    cursor: pointer;
    background-color: #f3f3f3;
    padding: 10px;
    max-width: 250px;
    text-align: left;
    margin: 0 auto; }
    .card:hover {
      background-color: #f3f3f3;
      border-color: #f3f3f3; }
    .card img {
      max-width: calc(100%);
      margin: 0 auto;
      display: block; }
    .card .index {
      background-color: rgba(0, 0, 0, 0.2);
      color: #fff;
      line-height: 20px;
      font-size: 14px;
      position: absolute;
      top: 0;
      right: 0;
      display: block;
      padding: 0 10px; }
    .card .price {
      margin: 0;
      padding: 10px 0;
      font-weight: bold; }
    .card .details {
      position: relative; }
    .card .features {
      list-style: none;
      padding: 0;
      margin: 0; }
        
  
  /* #endregion */
  /* #region cards slider */
  .cards-slider-wrapper{
    display:flex;
    position: absolute;
  }
  .card{
      flex:1;
      min-width: 200px;
    }
  
    .card-slider.active-slide-1#card-1{
      opacity:1;
    }
  
  
  .cards-slider {
    position: relative;
    max-width: 226px;
    margin: 0 auto; }
    .cards-slider .card {
      flex: 1; }
    .cards-slider:after {
      content: '';
      display: block;
      width: 88%;
      height: 335px;
      outline: 5px solid #011f4b;
      position: absolute;
      top: 0;
      left: 0; }
  
  /* #endregion */